.body{
    background-color: #f5f5f5 !important;
}
.redimension{
    width: 224px;
    height: 224px;
}
.redimensionDetail{
    width: 402px ;
    height: 500px ;
}
.pading1{
    padding-left: 12%;
}
.old-price {
    color: #f00;
    text-decoration: line-through;
}
.txtcardDolar{
    color: gray;
    margin-bottom: 0.5em;
}
#btnVerProducto{
    margin-top: 0.5em;
}

.btnComprar{
    width: 100%;
    margin-bottom: 10px;
}
.borderCard{
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0,0,0,.125);
    border-radius: .25rem;
}
.resetMargin{
    margin: 0px !important;
    padding: 0px !important;
}
.centerContent{
    padding: 10px;
}
#btnBack{
    margin-left: 80%;
}
.colorCard{
    background-color: #f5f5f5 !important;
    box-shadow: 0.5px 1px rgba(172, 172, 172, 0.493);
}
#loadingSpinner{
    display: none;
}
.thank-you-pop{
	width:100%;
 	padding:20px;
	text-align:center;
}
.thank-you-pop img{
	width:76px;
	height:auto;
	margin:0 auto;
	display:block;
	margin-bottom:25px;
}

.thank-you-pop h1{
	font-size: 42px;
    margin-bottom: 25px;
	color:#5C5C5C;
}
.thank-you-pop p{
	font-size: 20px;
    margin-bottom: 27px;
 	color:#5C5C5C;
}
.thank-you-pop h3.cupon-pop{
	font-size: 25px;
    margin-bottom: 40px;
	color:#222;
	display:inline-block;
	text-align:center;
	padding:10px 20px;
	border:2px dashed #222;
	clear:both;
	font-weight:normal;
}
.thank-you-pop h3.cupon-pop span{
	color:#03A9F4;
}
.thank-you-pop a{
	display: inline-block;
    margin: 0 auto;
    padding: 9px 20px;
    color: #fff;
    text-transform: uppercase;
    font-size: 14px;
    background-color: #8BC34A;
    border-radius: 17px;
}
.thank-you-pop a i{
	margin-right:5px;
	color:#fff;
}
#ignismyModal .modal-header{
    border:0px;
}
/*--thank you pop ends here--*/
